import React, { Component } from "react";
import Menu from "./MenuComponent";
import Header from "./HeaderComponent";

import Footer from "./FooterComponent";
import Home from "./HomeComponent";
import Contact from "./ContactComponent";
import About from "./AboutComponent";
import { Switch, Route, Redirect, withRouter } from "react-router-dom";
import DishDetail from "./DishDetailComponent";
import { connect } from "react-redux";
import {
  postComment,
  fetchDishes,
  fetchComments,
  fetchPromos,
  fetchLeaders,
  postFeedback,
} from "../redux/ActionCreators";
import { actions } from "react-redux-form";
import { TransitionGroup, CSSTransition } from "react-transition-group";
//doi addComment thanh postComment, addComment se khong con co the truy cap truc tiep tu MainCompo nua
//Thay vao do neu muon them nhan xet, ban se dang binh luan, vi vay ta dung postComment. Ban than addComment
//se duoc postComment su dung de them vao co so du lieu

//import { actions } from "react-redux-form";

//withRouter: dinh cau hinh React Component de ket noi voi Redux
const mapStateToProps = (state) => {
  return {
    dishes: state.dishes,
    comments: state.comments,
    promotions: state.promotions,
    leaders: state.leaders,
  };
};

//fetchDishes  y l 1 thunk, do  chng ta c th phn phi bng cch s dng dispatch d thc hin cng vic dispatch
//ta cn nh x n trong mapDispatchToProps  dispatch dishes tr nn kh dng cho Main Component m ta s dng. V vy trong Main, ta cn fetch cc dishes
//Vy ta fetch cc dishes  u. Ta nh n lifectycle method l componentDidMount
const mapDispatchToProps = (dispatch) => ({
  postComment: (dishId, rating, author, comment) =>
    dispatch(postComment(dishId, rating, author, comment)),
  fetchDishes: () => {
    dispatch(fetchDishes());
  },
  fetchComments: () => {
    dispatch(fetchComments());
  },
  fetchPromos: () => {
    dispatch(fetchPromos());
  },
  resetFeedbackForm: () => {
    dispatch(actions.reset("feedback"));
  },
  fetchLeaders: () => {
    dispatch(fetchLeaders());
  },
  postFeedback: (feedback) => {
    dispatch(postFeedback(feedback));
  },
});

class Main extends Component {
  constructor(props) {
    super(props);
  }

  //Bt c th g chng ta a vo y s c rng buc s c gi hoc s c
  //thc thi ngay sau khi component ny c gn vo view ng dng. V vy ngay ti
  //Gi hm fetchDishes, gi n l index, iu xy ra l khi Main c gn vo view ca ng dng React
  //Ti thi im sau khi c gn kt. fetchDished s c gi l kt qu l trong ln call  ly cc mn n
  // v sau  ti n vo redux store v sau khi iu  tr nn kh dng, sau  n s c sn trong ng dng
  componentDidMount() {
    this.props.fetchDishes();
    this.props.fetchComments();
    this.props.fetchPromos();
    this.props.fetchLeaders();
  }

  render() {
    const HomePage = () => {
      return (
        <Home
          dish={this.props.dishes.dishes.filter((dish) => dish.featured)[0]}
          dishesLoading={this.props.dishes.isLoading}
          dishesErrMess={this.props.dishes.errMess}
          promotion={
            this.props.promotions.promotions.filter(
              (promo) => promo.featured
            )[0]
          }
          promosLoading={this.props.promotions.isLoading}
          promosErrMess={this.props.promotions.errMess}
          leader={
            this.props.leaders.leaders.filter((leader) => leader.featured)[0]
          }
          leadersLoading={this.props.leaders.isLoading}
          leadersErrMess={this.props.leaders.errMess}
        />
      );
    };

    const DishWithId = ({ match }) => {
      return (
        <DishDetail
          dish={
            this.props.dishes.dishes.filter(
              (dish) => dish.id === parseInt(match.params.dishId, 10)
            )[0]
          }
          isLoading={this.props.dishes.isLoading}
          errMess={this.props.dishes.errMess}
          comments={this.props.comments.comments.filter(
            (comment) => comment.dishId === parseInt(match.params.dishId, 10)
          )}
          commentsErrMess={this.props.comments.errMess}
          postComment={this.props.postComment}
        />
      );
    };

    return (
      <div className="App">
        <Header />
        <TransitionGroup>
          <CSSTransition
            key={this.props.location.key}
            classNames="page"
            timeout={300}
          >
            <Switch>
              <Route path="/home" component={HomePage} />
              <Route
                path="/aboutus"
                component={() => <About leaders={this.props.leaders} />}
              />
              <Route
                exact
                path="/menu"
                component={() => <Menu dishes={this.props.dishes} />}
              />
              <Route path="/menu/:dishId" component={DishWithId} />
              <Route
                exact
                path="/contactus"
                component={() => (
                  <Contact
                    resetFeedbackForm={this.props.resetFeedbackForm}
                    postFeedback={this.props.postFeedback}
                  />
                )}
              />
              <Redirect to="/home" />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
        <Footer />
      </div>
    );
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));

